.signup {
  height: 170px;
  width: 320px;
  margin: auto;
  margin-top: 230px;
  padding: 30px;
  /* background-image: url(./img/bgig.jpg); */
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 5px;
  background-color: rgba(138, 43, 226, 0.5);
  box-shadow: 25px 25px 5px 0px rgb(7, 1, 28);
  animation: scale-in-ver-center 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

.signup div {
  margin-bottom: 30px;
}

.signup .label_1 label {
  margin-right: 33px;
}

.signup .label_2 label {
  margin-right: 35px;
}

.signup .label_3 label {
  margin-right: 10px;
}

.signup div input {
  width: 62%;
  border: none;
  border-radius: 10px;
  color: #ff57d5;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
  font-weight: bold;
  text-align: center;
  padding: 2px 0;
  font-weight: bold;
  background-color: #9bff9c;
}

.label_1>input:focus {
  outline: none;
}

.label_2>input:focus {
  outline: none;
}

.label_3>input:focus {
  outline: none;
}

.signup .submit {
  margin: auto;
  width: 100%;
}

.signup div button {
  width: 100%;
  border: none;
  border-radius: 15px;
  margin-top: 10px;
  color: #ff57d5;
  background-color: #9bff9c;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
  font-weight: bold;
}

.submit>button:active {
  color: #56a9ff;
}

@keyframes scale-in-ver-center {
  0% {
      -webkit-transform: scaleY(0);
      transform: scaleY(0);
      opacity: 1;
  }

  100% {
      -webkit-transform: scaleY(1);
      transform: scaleY(1);
      opacity: 1;
  }
}