.calculator {
    height: 460px;
    width: 330px;
    border: 5px groove black;
    background-color: darkslateblue;
    margin: auto;
    margin-top: 150px;
    margin-bottom: -5px;
    padding: 8px;
    border-radius: 10px
    
}

.calculator div {
    height: 60px;
    width: 330px;
    margin-top: 10px;
}

.calculator div input {
    border: none;
    height: 100%;
    width: 94%;
    background-color: skyblue;
    text-align: right;
    border-radius: 5px;
    margin-left: 8px;
}

.calculator div button {
    background-color: palevioletred;
    height: 70%;
    width: 20%;
    margin-right: 2%;
    margin-left: 2%;
    border-radius: 25%;
    font-size: large;
    color: gold;
    border: none;
}

.calculator .endbutton button {
    width: 45%;
    border-radius: 8%;
}