.home {
    background-color: black;
    height: 100vh;
    overflow: hidden;
    padding-left: 30px;
    color: #50c4ed;
    position: absolute;
    top: 0;
    z-index: -1;
  }
  .home h1 {
    margin-top: 45px;
    color: #50c4ed;
    animation: flicker-1 2s linear infinite both;
  }
  .home h2 {
    color: #fba834;
  }
  .home .intro {
    text-align: justify;
    text-indent: 30px;
  }
  
  .home p {
    padding-right: 10px;
  }
  .home button {
    border: none;
    border-radius: 5px;
    color: #fba834;
    background-color: #333a73;
    font-family: Arial, Helvetica, sans-serif;
    font-size: medium;
    font-weight: bold;
    padding: 10px;
  }
  @keyframes flicker-1 {
    0%,
    100% {
      opacity: 1;
    }
    41.99% {
      opacity: 1;
    }
    42% {
      opacity: 0;
    }
    43% {
      opacity: 0;
    }
    43.01% {
      opacity: 1;
    }
    47.99% {
      opacity: 1;
    }
    48% {
      opacity: 0;
    }
    49% {
      opacity: 0;
    }
    49.01% {
      opacity: 1;
    }
  }