.card {
    height: 300px;
    width: 200px;
    border: 2px solid rgb(40, 40, 176);
    display: inline-block;
    margin: 50px 25px 10px 25px;
    background-color: rgb(15, 119, 119);
    text-align: center;
}

.card img {
    height: 210px;
    width: 180px;
    background-color: white;
    margin-left: 10px;
    margin-top: 10px;
    
}

.card .sec {
    margin-left: 10px;
    margin-top: 10px;
}

.card .tr {
    margin-top: 10px;
    margin-left: 10px;
}
.card .tr p,button {
    display: inline-block;
}

.card .tr button {
    margin-left: 105px;
    border-radius: 5px;
    border: none;
    padding: 5px 10px;
    text-align: center;
}
.card p {
    margin: 0;
}