.login {
    height: 130px;
    width: 320px;
    margin-left:400px;
    
    margin-top: 100px;
    padding: 20px;
    /* background-image: url(./img/bgig.jpg); */
    background-repeat: no-repeat;
    background-size: cover;
    /* margin-bottom: 50px; */
    border-radius: 5px;
    /* box-shadow: 25px 25px 5px 0px rgb(7, 1, 28); */
    animation: scale-in-ver-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    border-color: black;
    border-style: solid;
}

.login div {
    margin-bottom: 25px;
    
}

.login .label_1 label {
    margin-right: 33px;
}

.login .label_2 label {
    margin-right: 35px;
}

.login div input {
    width: 60%;
    border: none;
    border-radius: 10px;
    color: #ff57d5;
    text-align: center;
    padding: 2px 0;
    font-family: Arial, Helvetica, sans-serif;
    font-size: medium;
    font-weight: bold;
    background-color: #9bff9c;
    margin-right: -10px;
}

.label_1>input:focus {
    outline: none;
    margin-top: 5px;
    margin-left: 100px;
}

.label_2>input:focus {
    outline: none;
}

.login .submit {
    margin: auto;
    width: 50%;
    margin-top: -1px;
    margin-right: -13px;

}

.login div button {
    width: 80%;
    border: none;
    border-radius: 10px;
    margin-top: 10px;
    /* margin-bottom: 23px; */
    /* /* margin-bottom:  */
    color: #ff57d5;
    background-color: #9bff9c;
    font-family: Arial, Helvetica, sans-serif;
    font-size: medium;
    font-weight: bold;
}


.submit>button:active {
    color: #56a9ff;
}

@keyframes scale-in-ver-center {
    0% {
        -webkit-transform: scaleY(0);
        transform: scaleY(0);
        opacity: 1;
    }

    100% {
        -webkit-transform: scaleY(1);
        transform: scaleY(1);
        opacity: 1;
    }
}
