.button {
  margin: auto;
  width: 850px;
  width: fit-content;
}
.button button {
  font-size: 20px;
  padding: 10px 20px;
  margin-right: 30px;
  border: none;
  border-radius: 5px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
  font-weight: bold;
  color: #333a73;
  background-color: #fba834;
  transition: all .5s;
}
.button button.activePage {
  background-color: #50c4ed;
  color: #333a73;
}
.button button:active {
    transform: scale(0.9);
}

.button button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.button select{
  font-size: 20px;
  padding: 10px 20px;
  margin-right: 30px;
  border: none;
  border-radius: 5px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
  font-weight: bold;
  color: #333a73;
  background-color: #fba834;
  outline: none;
  transition: all .5s;
}
.button select:active{
  transform: scale(.9);
}

.table {
  border-collapse: collapse;
  width: 90%;
  margin: 30px auto;
  color: #50c4ed;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
  font-weight: bold;
}
.table th {
  position: sticky;
  top: 40px;
  background-color: #333a73;
  text-transform: capitalize;
  font-size: larger;
  /* border: 2px solid #fba834; */
  padding: 30px;
  color: #387adf;
  outline: 2px solid #fba834;

}
.table td {
  border: 2px solid #fba834;
  padding: 30px;
  text-align: center;
}