.table  {
    /* border: 2px solid red; */
    border-collapse: collapse;
    width: 90%;
    margin: 30px auto;
    color: #50C4ED;
    font-family: Arial, Helvetica, sans-serif;
    font-size: medium;
    font-weight: bold;
}
.table tr:nth-child(1){
    position: sticky;
    top: 40px;
    background-color: #333A73;
}
.table th {
    border: 2px solid #FBA834;
    padding: 30px;
    color: #387ADF;
}
.table td {
    border: 2px solid #FBA834;
    padding: 30px;
    text-align: center;
}